.motto-container {
    width: 100%;
    height: 120vh;
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
    word-wrap: break-word;
    
    h1 {
        font-size: $x-large;
        color: $blk;
        text-transform: uppercase;
        font-family: $unbounded;
    }}