.landing-container {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    .logo-container {
        display: flex;
        flex-flow: column;

        &>div {
            display: flex;
        }
    }

    .text-container {
        width: 70%;
        font-size: $large;
        text-transform: uppercase;
        color: $blk;
        font-weight: 400;

        .line {
            margin: 5rem 0;
            letter-spacing: calc(1.5vw + 0.05rem);
            display: flex;
            justify-content: space-between;
            transition: all 0.5s cubic-bezier(0.25, 0.6, 0.45, 0.94);


            &:hover {
                letter-spacing: 0.05rem;
                transition: all 0.5s cubic-bezier(0.25, 0.6, 0.45, 0.94);
            }

            .word {
                margin: 0;
                padding: 0;
            }
        }
    }

    .explore-container {
        position: absolute;
        bottom: 0%;
        left: 50%;
        transform: translate(-50%, -5%);
        display: flex;
        width: 15rem;
        height: 15rem;  
        justify-content: center;
        align-items: center;

        &:after {
            content: "";
            position: absolute;
            bottom: 0;
            top: 70%;
            width: 0px;
            height: 5px;
            margin: 5px 0 0;
            transition: all 0.2s ease;
            transition-duration: 0.3s;
            opacity: 0;
            background-color: $blk;

        }
        &:hover::after {
            width: 100%;
            opacity: 1;
        }

        &>a, &>p {
            color: $blk;
            font-size:$large;
            user-select: none;
            margin: 0 1rem;
        }
    }
}

@media screen and (max-width:700px) {
    .text-container {
        width: 100% !important;
        margin: 0;
        padding: 0;
        font-size: calc(1rem + 2vw) !important;

        .line {
            margin: 10rem 0 !important;
        }
    }
}

@keyframes slide {
    from { left: 100%;}
    to { left: -100%;}
  }
  @-webkit-keyframes slide {
    from { left: 100%;}
    to { left: -100%;}
  }

.unbounded {
    font-family: $unbounded;
}

.bold {
    font-weight: 900;
}
.light {
    font-weight: 200;
}